import {checkFields, setCookie, getCookie, deleteCookie} from './helpers.js';


import initServiceScripts from '../blocks/service/service.js';
import initNavScripts from '../blocks/nav/nav.js';
import initBannerScripts from '../blocks/banner/banner.js';
import initReviewsScripts from '../blocks/reviews/reviews.js';
import initPlanScripts from '../blocks/plan/plan.js';
import initActionsScripts from '../blocks/actions-content/actions.js';
import initDoctorMoreScripts from '../blocks/doctor-education-more/doctorsm.js';
import initChildServiceScripts from '../blocks/child-service-steps/css.js';
import initReviewsContentScripts from '../blocks/reviews-content/reviews.js';
import initTestScripts from '../blocks/test/test.js';

(() => {
	initNavScripts();
	initServiceScripts();
	initBannerScripts();
	initActionsScripts();
	initReviewsScripts();
	initPlanScripts();
	initDoctorMoreScripts();
	initChildServiceScripts();
	initReviewsContentScripts();
	initTestScripts();
	
	if ($('#age').length) {
		const elem = document.getElementById('age');
		elem.oninput = function(e) {
			this.value = this.value.replace(/[^\d]/g,'');
		};
	}
	
	if ($('.wrapper-fix-form').length) {
		$('.wrapper-fix-form').stickySidebar({
			topSpacing: $('.header').outerHeight(),
			bottomSpacing: $('.footer').outerHeight() + 220
		});
	}
	
	$('.big-img-container').each(function() { // the containers for all your galleries
		$(this).magnificPopup({
			delegate: 'a.big-img',
			 type: 'image',
			mainClass: 'mfp-with-zoom', // this class is for CSS animation below

			zoom: {
				enabled: true, // By default it's false, so don't forget to enable it

				duration: 300, // duration of the effect, in milliseconds
				easing: 'ease-in-out', // CSS transition easing function
			},
			type: 'image',
				gallery: {
				enabled: true,
				tCounter: '<span class="mfp-counter"></span>'
			}
		});
	});
	
	function copyText(element) {
			var text = document.getElementById(element);
			var selection = window.getSelection();
			var range = document.createRange();
			range.selectNodeContents(text);
			selection.removeAllRanges();
			selection.addRange(range);
			document.execCommand('copy');
}
	
	$('.share-elem__copy').click(function(e) {
		const self = this;
		const text = $(self).find('.text').text();
		
		copyText('input1');
		$(self).find('.text').text('Скопировано');
		
		setTimeout(function(){
			$(self).find('.text').text(text);
		}, 1000);
		return false;
	});

	if ($('.banner').length) {
		$(window).resize(function() {
			setTimeout(function() {
				$('.banner-item__img').css('height', $('.banner-item__img img').height());
			}, 300);
		});
	}
	
	document.addEventListener('DOMContentLoaded', function () {
		if ($('.banner').length) {
			setTimeout(function() {
				$('.banner-item__img').css('height', $('.banner-item__img img').height());
			}, 300);
		}
		$('input[type="tel"]').inputmask("+7 999 999 99 99");
	});

	$(document).on('click', '.policy', function() {
		$(this).toggleClass('active');
	});
	
	$(document).on('focus click', '.required', function() {
		$(this).removeClass('error');
	});
	
	$(document).on('click', 'button[type=submit]', function (e) {
		const $form =  $(this).parents('form');
		let $btn = $(this);
		
		if(!checkFields($form)) {
			
			if(!$('input[name=send_to]').length) {
				$form.append('<input type="hidden" name="send_to" value="">');
			}
			
			$('input[name=send_to]').val($('.data-email').attr('data-email'));
			//change it
			if(!$btn.next().hasClass('active')) {
				alert('Форма не может быть отправлена. Вы не согласны на обработку персональных данных.');
				return false;
			}
			
			let $btnText = $btn.find('span').text();
			$btn.find('span').text('Обработка данных...');
			$btn.prop('disabled', true);
			const $method = $form.attr('method');
			const $action = $form.attr('action');
			const formData = new FormData($(this).parents('form')[0]);

			$.ajax({
				type: $method,
				url: $action,
				data: formData,
				processData: false,
				contentType: false,
				success: function(data) {
					$btn.find('span').text($btnText);
					setCookie('empty', 'val', { path: '/' });
					window.location.href = '/thank.html';
					$btn.prop('disabled', false);
				},
				error: function(data) {
					$btn.find('span').text('Ошибка');
					setTimeout(function() {
						$btn.find('span').text($btnText);
					}, 2000);
				}
			});
		} else {
			console.log('Error');
		}
		return false;
	});
})();



