import {checkFields} from '../../scripts/helpers.js';

export default function initTestScripts() {
	//const $control = $('.test-container');
	
	$('.test-container').each(function(i, item) {
			$(item).slick({
				accessibility: false,
				dots: false,
				arrows: false,
				draggable: false,
				swipe: false,
				infinite: false,
				slidesToShow: 1,
				slidesToScroll: 1,
				adaptiveHeight: true
			});
	});
	
	$(document).on('click', '.show-test', function () {
		let dataAttr = $(this).attr('data-id');
		let inst = $('[data-remodal-id=' + dataAttr + ']').remodal();
		
		$(window).trigger('resize');
		
		inst.open();
		
		return false;
	});
	
	$(document).on('click', '.tooth-btn', function () {
		$(this).parent().children().each(function(i, item) {
			$(this).removeClass('active');
		});
		$(this).addClass('active');
		if ($(this).hasClass('active')) {
			$('input[name=tooth]').val($(this).find('.tooth-btn__sign').text());
		}
	});
	
	$(document).on('click', '.step-qw', function() {
		if ($('.step-qws').length) {
			$(this).parents('.step-qws').find('.step-qw').each(function(i, item) {
				$(item).removeClass('active');
				$(item).find('input').prop( "checked", false );
			});
		}
		if (!$(this).find('input:checked').length) {
			$(this).addClass('active');
			$(this).find('input').prop( "checked", true );
		}
		if ($(this).find('input:checked').length) {
			$(this).find('input').val($(this).find('.step-qw__text').text());
		}
	});
	
	
	
	$(document).on('click', '.next-btn', function(e) {
		let $fields = $(this).parents('.test-step');
		if(!checkFields($fields)) {
			$(this).parents('.test-container').slick('slickNext');
		}
		
		return false;
	});
	
	$(document).on('click', '.prev-btn', function(e) {
		$(this).parents('.test-container').slick('slickPrev');
		return false;
	});
}


