export default function initServiceScripts() {
	setTimeout(function() {
		let layoutMode = $('.isotope-grid').attr('data-layout');
	
		const $grid = $('.isotope-grid').isotope({
			itemSelector: '.isotope-item',
			layoutMode: layoutMode,
			getSortData: {
				name: '.name',
				symbol: '.symbol',
				number: '.number parseInt',
				category: '[data-category]',
				weight: function( itemElem ) {
					let weight = $( itemElem ).find('.weight').text();
					return parseFloat( weight.replace( /[\(\)]/g, '') );
				}
			}
		});
		$(document).on( 'click', '.isotope-btn', function(e) {
			e.preventDefault();
			if ($(this).hasClass('active')) {
				return false;
			}

			if ($(window).width() < 1050) { 
				$('html, body').animate({
				scrollTop: $('.isotope-grid').offset().top - 15
					}, {
						duration: 500
				});
			}

			let filterValue = $(this).attr('data-filter');

			$('[data-filter]').each(function(i, item) {
				$(item).removeClass('active');
			});

			$('[data-filter="' + filterValue + '"]').each(function(i, item) {
				$(item).addClass('active');
			});

			$grid.isotope({ filter: filterValue });
		});
	}, 300);
	
	function shopContent() {
		if ($('.shop-content').length) {
			if ($(window).width() < 790) {
				$('.shop-content .child-service-steps__container').attr('data-layout', 'vertical');
			} else {
				$('.shop-content .child-service-steps__container').attr('data-layout', 'fitRows');
			}
		}
	}
	
	function initSlider() {
		if ($(window).width() < 1050) {
			$('.service__type').each(function(i, item) {
				if (!$(item).hasClass('slick-initialized')) {
					$(item).slick({
						dots: false,
						infinite: true,
						slidesToShow: 1,
						slidesToScroll: 1,
						prevArrow: $(item).parent().find('.banner-arrow.arr-l'),
						nextArrow: $(item).parent().find('.banner-arrow.arr-r'),
					});
				}
			});
		} else {
			$('.service__type').each(function(i, item) {
				if ($(item).hasClass('slick-initialized')) {
					$(item).slick('unslick');
				}
			});
		}
	}
	
	initSlider();
	shopContent();
	$(window).resize(function() {
		initSlider();
		shopContent();
	});
}


