export default function initActionsScripts() {
	if($('.actions-banners').length) {
		$('.actions-banner-slider').slick({
			dots: true,
			infinite: true,
			slidesToShow: 1,
			slidesToScroll: 1,
			prevArrow: $('.actions-banners .banner-arrow.arr-l'),
			nextArrow: $('.actions-banners .banner-arrow.arr-r')
		});
	}
}


