export default function initBannerScripts() {
	if($('.banner').length) {
		$('.banner-slider').slick({
			dots: false,
			infinite: true,
			slidesToShow: 1,
			slidesToScroll: 1,
			fade: true,
			cssEase: 'linear',
			prevArrow: $('.banner-wrapper .banner-arrow.arr-l'),
			nextArrow: $('.banner-wrapper .banner-arrow.arr-r')
		});
	}
}


