export default function initPlanScripts() {
	if($('.plan-examples').length) {
		$('.plan-slider').slick({
			dots: true,
			infinite: true,
			slidesToShow: 1,
			slidesToScroll: 1,
			prevArrow: $('.plan-examples .banner-arrow.arr-l'),
			nextArrow: $('.plan-examples .banner-arrow.arr-r')
		});
	}
}


