export default function initReviewsContentScripts() {
	$(document).on('click', '.filter-review__title, .filter-blog__title', function() {
		$(this).toggleClass('active');
		$(this).next().slideToggle();
	});
	
	$(document).on('click', '.filter-review-item', function() {
		$(this).toggleClass('active');
	});
	
	$(document).on('click', '.reviews-search__btn', function(e) {
		e.preventDefault();
		if ($(window).width() < 1068) {
			$('html, body').animate({
				scrollTop: $('.reviews-content__content').offset().top - 15
						}, {
					duration: 500
			});
		}
	});
	
	$(document).on('click', '.reviews-content-btn', function(e) {
		e.preventDefault();
		
		if ($(this).hasClass('active')) {
			return false;
		}

		$('.reviews-content-btn').each(function(i, item) {
			$(item).removeClass('active')
		});
		
		$(this).addClass('active');
		
		$('html, body').animate({
			scrollTop: $('.reviews-content').offset().top - 15
					}, {
				duration: 500
		});
	
		$('.reviews-content').children().each(function(i, item) {
			$(item).removeClass('active')
		});
		
		$('.reviews-content').find($(this).attr('data-filter')).addClass('active');
		
	});
	
}


