export default function initReviewsScripts() {
	if($('.reviews').length) {
		$('.reviews-slider').slick({
			dots: true,
			infinite: true,
			slidesToShow: 1,
			slidesToScroll: 1,
			prevArrow: $('.reviews-wrapper .banner-arrow.arr-l'),
			nextArrow: $('.reviews-wrapper .banner-arrow.arr-r')
		});
	}
}


