export default function initChildServiceScripts() {
	if($('.child-service-steps-slider').length) {
		$('.child-service-steps-slider').slick({
			dots: true,
			infinite: true,
			slidesToShow: 3,
			slidesToScroll: 1,
			arrows: false,
			autoplay: true,
			autoplaySpeed: 3000,
			responsive: [
			{
				breakpoint: 875,
				settings: {
					slidesToShow: 1,
				}
			}
		]
		});
	}
	
	if($('.tech-gallery-slider').length) {
		$('.tech-gallery-slider').slick({
			dots: true,
			infinite: true,
			slidesToShow: 3,
			slidesToScroll: 1,
			autoplay: true,
			autoplaySpeed: 3000,
			prevArrow: $('.tech-gallery .banner-arrow.arr-l'),
			nextArrow: $('.tech-gallery .banner-arrow.arr-r'),
			responsive: [
			{
				breakpoint: 875,
				settings: {
					slidesToShow: 1,
				}
			}
		]
		});
	}
}


