export default function initDoctorMoreScripts() {
	if($('.doctor-diploms').length) {
		$('.doctor-diploms-slider').slick({
			dots: true,
			infinite: true,
			slidesToShow: 3,
			slidesToScroll: 1,
			prevArrow: $('.doctor-diploms .banner-arrow.arr-l'),
			nextArrow: $('.doctor-diploms .banner-arrow.arr-r'),
			responsive: [
			{
				breakpoint: 875,
				settings: {
					slidesToShow: 1,
				}
			}
		]
		});
	}
}


